import React, { useEffect, useRef, useState } from 'react';

import { message } from 'antd';

var JsBarcode = require('jsbarcode');
// npm install jsbarcode --save

const ZwBarcodeCanvas = ({codeType, text, width, height, fontSize, onClick}) => {
    // 条码
    const codeCanvas = useRef(null);

    useEffect(() => {
        const currentOptions = {
            width: width,
            height: height,
            displayValue: true,
            fontSize: fontSize,
        }
        let currentCodeType = ''
        if (codeType.length > 0) {
            currentCodeType = codeType;
            const notUpperCaseTypes = ['codabar', 'auto', 'pharmacode'];
            if (notUpperCaseTypes.indexOf(codeType) < 0) currentCodeType = currentCodeType.toUpperCase();
            currentOptions.format = currentCodeType;
        }
        console.log(currentOptions);
        createBarcode(currentOptions);

    }, [codeType, text, width, height, fontSize]);

    const createBarcode = (optinons) => {
        // 创建条形码
        try {
            JsBarcode(codeCanvas.current, text, optinons);
        } catch (error) {
            console.error(error);
            message.error('条码错误...');
        }
    }

    return (
        <canvas ref={codeCanvas} onClick={onClick}></canvas>
    )
};
ZwBarcodeCanvas.defaultProps = {
    codeType: '',
    text: '123456',
    width: 1,
    height: 100,
    fontSize: 15,
}

export { ZwBarcodeCanvas };
