import React, { useEffect, useRef} from 'react';
import QRious from 'qrious';
// 安装 QRCode: npm install --save qrious

const ZwCanvasQrcode = ({urlText, size, onClick}) => {
    const qrcanvas = useRef(null);

    useEffect(() => {
        if (qrcanvas != null && qrcanvas.current != null) {
            const doneCanvasQr = new QRious({
                element: qrcanvas.current,
                background: '#ffffff',
                backgroundAlpha: 1,
                foreground: 'black',
                foregroundAlpha: 1,
                level: 'H',
                padding: 0,
                size: size,
                value: urlText
            });           
        }

    }, [urlText, size]);

    return(
        <canvas onClick={onClick} ref={qrcanvas}></canvas>
    )
};
ZwCanvasQrcode.defaultProps = {
    urlText: 'http://localhost:8000/',
    size: 60,
    onClick: e => console.log(e.target),
};

export default ZwCanvasQrcode;
