import React, { useEffect, useState, useRef } from 'react';

import './zw_div.css';

const ZwDivArrow = ({children, title, visible, setVisible, visibleNumber, divLocation, mbNumber}) => {
    // 带箭头的 div
    const [displayVal, setDisplayVal] = useState('hidden');
    const [topMm, setTopMm] = useState('40px');
    const [leftMm, setLeftMm] = useState('30px');

    const [arrowMarginTopNumber, setArrowMarginTopNumber] = useState(6);

    const arrowDiv = useRef()

    useEffect(() => {
        // console.log(divLocation);
        if (visible === visibleNumber) {
            setDisplayVal('block');
        } else {
            setDisplayVal('hidden');
        }

    }, [visible, visibleNumber]);

    const computeArrowTopMargin = (divTop, top) => {
        return parseInt(top) - parseInt(divTop) - 20;
    };

    const computeDivTop = (divHeight, topNum, currentMbNumber) => {
        // 计算div顶部位置
        // console.log(divHeight, topNum, currentMbNumber);
        let top = topNum - divHeight + currentMbNumber;
        if (top < 10) top = 10;
        console.log(divHeight, topNum, currentMbNumber, top);
        return top;
    };

    useEffect(() => {
        console.log(divLocation);
        let divHeight = arrowDiv.current.clientHeight;
        if (divHeight < 50) divHeight = 200;
        const {top, left} = divLocation;
        const divTopNum = computeDivTop(divHeight, top, mbNumber);
        setTopMm(`${divTopNum}px`);
        setLeftMm(`${left}px`);
        const mt = computeArrowTopMargin(divTopNum, top);
        setArrowMarginTopNumber(mt);
    }, [divLocation, mbNumber]);

    return (
        <div className={`relative flex justify-start w-auto h-auto ${displayVal} p-4 z-40`} style={{position: 'fixed', top: topMm, left: leftMm}}>
            <div className="arrow-left static h-auto" style={{'marginTop': `${arrowMarginTopNumber}px`}}></div>
            <div ref={arrowDiv} className="rounded-lg border-2 border-gray-500 h-auto min-w-min bg-gray-100">
                <div className="flex justify-between items-center w-full h-8 bg-gray-100 p-2 mb-0 rounded-t-lg">
                    <div className="text-base subpixel-antialiased font-medium mr-8">{title}</div>
                    <div className="flex items-center h-full">
                        <button className="btn btn-circle btn-xs btn-ghost" onClick={() => setVisible(0)}>
                            <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" className="inline-block w-4 h-4 stroke-current md:w-6 md:h-6">   
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M6 18L18 6M6 6l12 12"></path>                       
                            </svg>
                        </button>
                    </div>
                </div>
                <div className="p-4 bg-base-100">{children}</div>
            </div>
        </div>
    )
};

ZwDivArrow.defaultProps = {
    children: null,
    title: '',
    visible: 0,
    setVisible: (num) => console.log(num),
    visibleNumber: 1,
    divLocation: { top: 20, left: 280 },
    mbNumber: 20,
};

export default ZwDivArrow;
