import React from 'react';


const ZwToggleLine = ({labelText='', checked=false, setChecked, width}) => {
    return (
        <div className="p-2 card bg-base-200 m-2" style={{width: `${width + 2}rem`}}>
            <div className="form-control" style={{width: `${width}rem`}}>
                <label className="cursor-pointer label">
                    <span className="label-text">{labelText}</span>
                    <input type="checkbox" defaultChecked={checked} onClick={() => setChecked(!checked)}  className="toggle toggle-accent toggle-sm" />
                </label>
            </div>
        </div>
    )
};
ZwToggleLine.defaultProps = {
    labelText: '',
    checked: false,
    setChecked: (trueOrFalse) => console.log(trueOrFalse),
    width: '16',
}

export default ZwToggleLine;
